
"use client";

import { useState } from "react";
import { vehicles as initialVehicles, drivers as allDrivers, conversations } from "@/lib/data";
import { DataTable } from "@/components/data-table/data-table";
import { columns } from "./columns";
import { PageHeader } from "@/components/page-header";
import { Button } from "@/components/ui/button";
import { PlusCircle, MessageSquare } from "lucide-react";
import { CreateVehicleDialog } from "@/components/create-vehicle-dialog";
import type { Vehicle, Conversation, Message } from "@/lib/types";
import { FleetDataTableToolbar } from "./toolbar";
import {
  ContextMenu,
  ContextMenuContent,
  ContextMenuItem,
  ContextMenuTrigger,
} from "@/components/ui/context-menu";
import { BroadcastMessageDialog } from "@/components/broadcast-message-dialog";
import { useToast } from "@/hooks/use-toast";

export default function FleetPage() {
  const { toast } = useToast();
  const [vehicles, setVehicles] = useState<Vehicle[]>(initialVehicles);
  const [isCreateDialogOpen, setIsCreateDialogOpen] = useState(false);
  const [isBroadcastDialogOpen, setIsBroadcastDialogOpen] = useState(false);
  const [selectedVehicle, setSelectedVehicle] = useState<Vehicle | null>(null);
  const [rowSelection, setRowSelection] = useState({});

  const handleCreateNew = () => {
    setSelectedVehicle(null);
    setIsCreateDialogOpen(true);
  };

  const handleEdit = (vehicle: Vehicle) => {
    setSelectedVehicle(vehicle);
    setIsCreateDialogOpen(true);
  };

  const handleDelete = (vehicleId: string) => {
    if (!window.confirm("Are you sure you want to delete this vehicle? This action cannot be undone.")) {
      return;
    }
    setVehicles(prev => prev.filter(v => v.id !== vehicleId));
    // In a real app, you would also call an API to delete from the database
  };

  const handleDialogSubmit = (vehicleData: Vehicle) => {
    if (selectedVehicle) {
      // Update
      setVehicles(prev => prev.map(v => (v.id === selectedVehicle.id ? vehicleData : v)));
    } else {
      // Create
      setVehicles(prev => [vehicleData, ...prev]);
    }
    setSelectedVehicle(null);
  };

  const handleBroadcastSubmit = ({ subject, message }: { subject: string; message: string }) => {
    const selectedIndices = Object.keys(rowSelection);
    const selectedVehicles = selectedIndices.map(index => vehicles[parseInt(index)]);

    let driversMessagedCount = 0;
    const broadcastContent = `Broadcast: ${subject}\n\n${message}`;

    selectedVehicles.forEach(vehicle => {
      if (vehicle.driverId) {
        const driver = allDrivers.find(d => d.id === vehicle.driverId);
        if (driver) {
          driversMessagedCount++;
          let conversation = conversations.find(c => c.contactName === driver.user.name);
          
          const newMessage: Message = {
            id: `msg-${Date.now()}-${driver.id}`,
            sender: 'me',
            content: broadcastContent,
            timestamp: new Date().toISOString(),
          };

          if (conversation) {
            conversation.messages.push(newMessage);
            conversation.lastMessage = broadcastContent;
            conversation.lastMessageTime = newMessage.timestamp;
            conversation.isUnread = false; // We sent the last message
          } else {
            const newConversation: Conversation = {
              id: `conv-${Date.now()}-${driver.id}`,
              contactName: driver.user.name,
              contactType: 'Driver',
              lastMessage: broadcastContent,
              lastMessageTime: newMessage.timestamp,
              messages: [newMessage],
              isUnread: false,
            };
            conversations.unshift(newConversation);
          }
        }
      }
    });


    toast({
      title: "Broadcast Sent!",
      description: `Your message has been sent to ${driversMessagedCount} driver(s).`,
    });
    setIsBroadcastDialogOpen(false);
    setRowSelection({});
  };

  const selectedRowCount = Object.keys(rowSelection).length;

  return (
    <div>
      <PageHeader 
        title="Fleet Management" 
        description="Oversee and manage all vehicles in the fleet."
        action={
          <Button onClick={handleCreateNew}>
            <PlusCircle className="mr-2 h-4 w-4" />
            Add Vehicle
          </Button>
        }
      />
      <ContextMenu>
        <ContextMenuTrigger>
          <DataTable 
            columns={columns({ onEdit: handleEdit, onDelete: handleDelete })} 
            data={vehicles}
            toolbar={FleetDataTableToolbar}
            rowSelection={rowSelection}
            onRowSelectionChange={setRowSelection}
          />
        </ContextMenuTrigger>
        <ContextMenuContent>
          <ContextMenuItem 
            onSelect={() => setIsBroadcastDialogOpen(true)}
            disabled={selectedRowCount === 0}
          >
            <MessageSquare className="mr-2 h-4 w-4" />
            Broadcast Message ({selectedRowCount} selected)
          </ContextMenuItem>
        </ContextMenuContent>
      </ContextMenu>
      
      <CreateVehicleDialog
        isOpen={isCreateDialogOpen}
        onOpenChange={setIsCreateDialogOpen}
        onVehicleSubmit={handleDialogSubmit}
        vehicle={selectedVehicle}
      />
      <BroadcastMessageDialog
        isOpen={isBroadcastDialogOpen}
        onOpenChange={setIsBroadcastDialogOpen}
        selectedVehiclesCount={selectedRowCount}
        onSubmit={handleBroadcastSubmit}
      />
    </div>
  );
}
